


























import { Component, Vue, Watch } from 'vue-property-decorator';
import { Originals, OriginalApply} from '@/entry/diamond';
import {Panel, Button, RadioGroup, Radio} from 'vant';
import {notes, NoteType} from '@/note';
import 'vant/lib/panel/style';
import 'vant/lib/button/style';
import 'vant/lib/radio-group/style';
import 'vant/lib/radio/style';


Vue.use(RadioGroup);
Vue.use(Radio);
Vue.use(Panel);
Vue.use(Button);
@Component
export default class Input extends Vue {
    public choiced = '授权原始';
    public id = '0';
    public inputName = '';
    public inputAddr = '';
    public disableCancel = false;
    public bgColor = '#DFF4EF';

    @Watch('choiced', {immediate: true, deep: true})
    public onChoicedChange(val: string):void {
        if (val == '授权原始') {
            this.bgColor = '#DFF4EF';
        } else {
            this.bgColor = '#EED1D1';
        }
    }

    public created(): void {
        interface org {
            name: string;
            addr: string;
            status: string;
        }
        if (this.$store.state.data.origins.length == 0) {
            this.$store.state.data.diamond.originalList().then((orgs: Originals) => {
                if (orgs.originalOne) {
                    const one: org = {
                        name: orgs.nameOne,
                        addr: orgs.originalOne,
                        status: this.$t('originalStatus.unSign').toString(),
                    };
                    this.$store.state.data.origins.push(one);
                }
                if (orgs.originalTwo) {
                    const two: org = {
                        name: orgs.nameTwo,
                        addr: orgs.originalTwo,
                        status:this.$t('originalStatus.unSign').toString(),
                    };
                    this.$store.state.data.origins.push(two);
                }
                if (orgs.originalThree) {
                    const three: org = {
                        name: orgs.nameThree,
                        addr: orgs.originalThree,
                        status:this.$t('originalStatus.unSign').toString(),
                    };
                    this.$store.state.data.origins.push(three);
                }
                let num = 0;
                for (const i of this.$store.state.data.origins) {
                    if (i.status == '已签名') {
                        num++;
                    }
                }
                if (this.$store.state.data.origins.length <= 2 && num <= 1) {
                    // alert(this.$store.state.data.origins.length);
                    this.disableCancel = true;

                }
            });
        } else {
        }
        this.$store.state.title = this.$t('original.input');


    }
    public signO(index: number):void {
        // 判断名字地址输入不能为空
        if (this.inputAddr && this.inputName) {
            // 判断store名字地址是否为空
            // 判断无效输入
            // 为空则赋值
            // 不为空则判断与输入地址是否已签名输入的是否相等
            // todo 改名操作
            for (let i = 0; i < this.$store.state.data.origins.length; i++) {
                if (this.$store.state.data.origins[i].name == this.inputName && this.$store.state.data.origins[i].addr == this.inputAddr) {
                    notes.add({type: NoteType.Error, message: this.$t('notifyMsg.sameOperationInvalid').toString()});
                    break;
                } else {
                    if (!this.$store.state.data.inputName && !this.$store.state.data.inputAddr) {
                        this.$store.state.data.inputName = this.inputName;
                        this.$store.state.data.inputAddr = this.inputAddr;
                        this.$store.state.data.radio = this.choiced;
                        this.$router.push({
                            name: 'original_apply',
                            params: {
                                ind: index.toString(),
                                radio: this.choiced,
                                name: this.inputName,
                                addr: this.inputAddr,
                            },
                        });
                    } else {
                        if (this.$store.state.data.inputName == this.inputName && this.$store.state.data.inputAddr == this.inputAddr && this.$store.state.data.radio == this.choiced) {
                            this.$router.push({
                                name: 'original_apply',
                                params: {
                                    ind: index.toString(),
                                    radio: this.choiced,
                                    name: this.inputName,
                                    addr: this.inputAddr,
                                },
                            });
                        } else {
                            notes.add({type: NoteType.Error, message: this.$t('notifyMsg.beConsistentWithSigned').toString()});
                        }
                    }
                }
            }
    } else {
            notes.add({type: NoteType.Error, message:this.$t('notifyMsg.inputNotNull').toString()});
        }
    }
    private makeChain():void {
        let num = 0;
        for (const i of this.$store.state.data.origins) {
            if (i.status == '已签名') {
                num++;
            }
        }
        if (num >= 2) {
            // 构造originalApply||store
            // let apply = this.$store.state.data.argApply;
            // 调用接口
            this.$store.state.data.diamond.chainOriginal(this.$store.state.data.orgApply).then((status: string) => {
                if (status == 'Success') {
                    this.$router.push({name: 'original_process', params: {}});
                }
            });
        } else {
            notes.add({type: NoteType.Error, message: this.$t('notifyMsg.inputTwoSign').toString()});
        }
    }
}
